import { useCallback, useState } from "react";
import { useStudentProfileAboutDetail } from "../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import BorderBottom from "../../Ui/Border/BorderBottom";
import style from "./StudentProfile.module.css";
import { pdfCdnUrl } from "../../Services/UrlConfig/BaseUrl";
import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import FormRaggingUnderTaking from "../../../JoiningForm/Student/FormRaggingUnderTaking";
import { useLocation } from "react-router-dom";
import CreateButton from "../../Ui/Button/CreateButton";
import SelectFeeStructure from "../../UserStaffMember/FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";
import AdmissionApplicationReject from "../../UserStaffMember/AdmissionManager/OneOngoingAdmission/AdmissionApplication/AdmissionApplicationReject";
import { useAdmissionOngoingApplicationSelect } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../Loader/QvipleLoader";
import AdmissionApplicationFormUpdateReject from "../../UserStaffMember/AdmissionManager/OneOngoingAdmission/AdmissionApplication/AdmissionApplicationFormUpdateReject";
import CustomCreateButton from "../../Ui/Button/CustomCreateButton";
const StudentDynamicProfileAbout = ({ studentId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [openDialog, setOpenDialog] = useState("");
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [whichSelect, setWhichSelect] = useState(true);
  const [admissionOngoingApplicationSelect] =
    useAdmissionOngoingApplicationSelect();
  const { studentProfileAboutDetail, studentProfileAboutDetailLoading } =
    useStudentProfileAboutDetail({
      data: {
        sid: studentId,
      },
      skip: !studentId,
    });

  const onViewFile = useCallback((iKey) => {
    if (iKey) {
      const newWindow = window.open(
        `${pdfCdnUrl}/${iKey}`,
        "_blank",
        "noopener noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  }, []);
  const onClose = () => {
    setStep("");
  };
  const onSelectStudent = () => {
    setStep(1);
  };
  const onCancelStudent = () => {
    setStep(2);
  };
  const onFormCancelStudent = () => {
    setStep(3);
  };
  const onSelectedStrucutre = (feeStructure) => {
    if (
      getQuery.state?.isExtraData?.sid &&
      getQuery.state?.isExtraData?.appId &&
      feeStructure?._id
    ) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationSelect({
        sid: getQuery.state?.isExtraData?.sid,
        appId: getQuery.state?.isExtraData?.appId,
        applicant: {
          fee_struct: feeStructure?._id,
        },
      })
        .then(() => {
          onClose();
          setWhichSelect(false);
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  return (
    <>
      {openDialog && (
        <FormRaggingUnderTaking
          onClose={() => setOpenDialog("")}
          value={openDialog}
        />
      )}
      <section className={style.sdpa}>
        {studentProfileAboutDetail?.map((sl, index) => (
          <div className={style.sdpa_section} key={`${sl?.key}${index}`}>
            <h5>
              {sl?.key ?? ""}
              {[
                "student_undertakings",
                "student_anti_ragging",
                "student_anti_ragging_parents",
              ]?.includes(sl?.fields?.[0]?.form_checklist_key) ? (
                <span
                  style={{
                    cursor: "pointer",
                    color: "#00f",
                    marginLeft: "0.3rem",
                  }}
                  onClick={() =>
                    setOpenDialog({
                      label: sl?.fields?.[0]?.form_checklist_name,
                      conent: sl?.fields?.[0]?.value,
                    })
                  }
                >
                  View Sample
                </span>
              ) : null}
            </h5>

            {sl?.static_key === "academic_details" ? (
              <>
                {sl?.fields?.map((fl, f_index) => (
                  <div
                    className={style.sdpa_section}
                    key={`${fl?.form_checklist_key}${f_index}`}
                    style={{
                      marginBottom: "0.5rem",
                    }}
                  >
                    <h5
                      style={{
                        fontSize: "13px",
                        marginBottom: "0.1rem",
                      }}
                    >
                      {fl?.form_checklist_name ?? ""}
                    </h5>
                    {[
                      "std_tenth_details",
                      "hsc_diploma",
                      "fyjc",
                      "fy_sem_I",
                      "fy_sem_II",
                      "sy_sem_III",
                      "sy_sem_IV",
                      "ty_sem_V",
                      "ty_sem_VI",
                      "msc_mcom",
                    ]?.includes(fl?.form_checklist_key) ? (
                      <div className={style.sdpac_wrap}>
                        {fl?.nested_form_checklist?.map((gl, g_index) => (
                          <div
                            className={style.sdpas_container}
                            key={`${gl?.form_checklist_key}${g_index}`}
                          >
                            <h6 className={style.sdpasc_label}>
                              {gl?.form_checklist_name ?? ""} :
                            </h6>
                            <h6 className={style.sdpasc_value}>
                              {gl?.value ?? ""}
                            </h6>
                          </div>
                        ))}
                      </div>
                    ) : (
                      fl?.nested_form_checklist?.map((gl, g_index) => (
                        <div
                          className={style.sdpa_section}
                          key={`${gl?.form_checklist_key}${g_index}`}
                          style={{
                            marginBottom: "0.5rem",
                          }}
                        >
                          <h5
                            style={{
                              fontSize: "13px",
                              marginBottom: "0.1rem",
                            }}
                          >
                            {gl?.form_checklist_name ?? ""}
                          </h5>
                          <div className={style.sdpac_wrap}>
                            {gl?.nested_form_checklist_nested?.map(
                              (tl, t_index) => (
                                <div
                                  className={style.sdpas_container}
                                  key={`${tl?.form_checklist_key}${t_index}`}
                                >
                                  <h6 className={style.sdpasc_label}>
                                    {tl?.form_checklist_name ?? ""} :
                                  </h6>
                                  <h6 className={style.sdpasc_value}>
                                    {tl?.value ?? ""}
                                  </h6>
                                </div>
                              )
                            )}
                          </div>
                        </div>
                      ))
                    )}
                  </div>
                ))}
              </>
            ) : (
              <div className={style.sdpac_wrap}>
                {sl?.fields?.map((fl, f_index) =>
                  fl?.form_checklist_typo === "FILE" ||
                  fl?.form_checklist_typo === "CROPIMAGE" ? (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      {fl?.value ? (
                        <h6
                          className={style.sdpasc_value}
                          onClick={() => onViewFile(fl?.value)}
                          style={{
                            cursor: "pointer",
                            color: "#002BBE",
                          }}
                        >
                          {t("view_file")}
                        </h6>
                      ) : // <a
                      //   className={style.sdpasc_value}
                      //   // onClick={() => onViewFile(fl?.value)}
                      //   href={`${imageShowUrl1}/${fl?.value}`}
                      //   download={false}
                      //   d
                      //   style={{
                      //     cursor: "pointer",
                      //     color: "#002BBE",
                      //     textDecoration: "none",
                      //   }}
                      // >
                      //   {t("view_file")}
                      // </a>
                      null}
                    </div>
                  ) : [
                      "student_undertakings",
                      "student_anti_ragging",
                      "studentProfilePhoto",
                      "student_anti_ragging_parents",
                    ]?.includes(fl?.form_checklist_key) ? null : (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      <h6 className={style.sdpasc_value}>{fl?.value ?? ""}</h6>
                    </div>
                  )
                )}
              </div>
            )}

            <BorderBottom />
          </div>
        ))}
        {studentProfileAboutDetailLoading && <Loading />}

        {getQuery.state?.isExtraInfo === "ONGOING_APPLICATION" &&
        whichSelect ? (
          <div className={style.sdpac_wrap}>
            {getQuery.state?.isExtraData?.reject_status ? (
              <CustomCreateButton
                isTranslate
                label={getQuery.state?.isExtraData?.reject_status}
                onAction={() => {}}
                customStyle={{
                  padding: "0.4rem 0.7rem",
                  fontSize: "13px",
                  "--ccb-bg": "#e6686830",
                  "--ccb-b": "#ed4a4a",
                  "--ccb-text": "#f00b0b",
                  "--ccb-bg-h": "#eb3232",
                  "--ccb-text-h": "#ffffff",
                  width: "100%",
                  justifyContent: "center",
                }}
              />
            ) : (
              <>
                <CreateButton
                  label="select"
                  customStyle={{
                    paddingBlock: "0.7rem",
                    margin: "0",
                    width: "32%",
                  }}
                  onAction={onSelectStudent}
                />
                <CreateButton
                  label="reject"
                  customStyle={{
                    paddingBlock: "0.7rem",
                    margin: "0",
                    width: "32%",
                  }}
                  onAction={onCancelStudent}
                />
                <CreateButton
                  label="reject_insufficient"
                  customStyle={{
                    paddingBlock: "0.7rem",
                    margin: "0",
                    width: "32%",
                  }}
                  onAction={onFormCancelStudent}
                />
              </>
            )}
          </div>
        ) : null}
      </section>
      {step === 1 ? (
        <SelectFeeStructure
          title="select_fee_structre_of_student"
          onClose={onClose}
          onSelectedStrucutre={onSelectedStrucutre}
          did={getQuery.state?.isExtraData?.did}
          bid={getQuery.state?.isExtraData?.bid}
          cmid={getQuery.state?.isExtraData?.cmid}
        />
      ) : step === 2 ? (
        <AdmissionApplicationReject
          onClose={onClose}
          appId={getQuery.state?.isExtraData?.appId}
          sid={getQuery.state?.isExtraData?.sid}
          isProfile
          setWhichSelect={setWhichSelect}
        />
      ) : step === 3 ? (
        <AdmissionApplicationFormUpdateReject
          onClose={onClose}
          appId={getQuery.state?.isExtraData?.appId}
          sid={getQuery.state?.isExtraData?.sid}
          isProfile
          setWhichSelect={setWhichSelect}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentDynamicProfileAbout;
